.slider {
  display: flex;
  overflow: hidden;
}


.slide {
  max-height: 60vh;
  min-height: 15rem;
  display: grid;
  gap: 1rem;
  background-color: orange;
  border-radius: 1rem;
  // height: 100%;
  // align-content: start;
}



#keen-slider .post-meta>* {
  height: 12rem;
  width: 20rem;
  height: min(12rem, 48vw) !important;
  width: min(20rem, 70vw) !important;
  overflow: hidden;
  border-radius: .5rem;
  margin-inline: auto;
}

#keen-slider .post-tags {
  pointer-events: none;
}

.keen-slider:not([data-keen-slider-disabled]) {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  align-content: flex-start;
  display: flex;
  overflow: hidden;
  position: relative;
  touch-action: pan-y;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  width: 100%
}

.keen-slider:not([data-keen-slider-disabled]) .keen-slider__slide {
  min-height: 100%;
  overflow: hidden;
  position: relative;
  width: 100%
}

.keen-slider:not([data-keen-slider-disabled])[data-keen-slider-reverse] {
  flex-direction: row-reverse
}

.keen-slider:not([data-keen-slider-disabled])[data-keen-slider-v] {
  flex-wrap: wrap
}




/* arrows */

@media screen and (max-width:650px) {
  .arrow {
    display: none;
  }
}

.arrow {
  isolation: isolate;
  position: absolute;
  top: 0;
  width: 40px;
  height: 100%;
  transform: none;
  translate: 0;

  z-index: 10;

  border: none;
  cursor: pointer;
  background-color: white;


  opacity: .7;
  transition: opacity .2s ease;
}

.arrow:hover {
  opacity: 1;
}


.arrow--left {
  left: 0;
}

.arrow--right {
  right: 0;
}

.arrow:after {
  content: '';
  position: absolute;
  background: 0 0;
  width: 20px;
  height: 20px;
  border-color: black;
  border-style: solid;
  transform: rotate(45deg);
}

.arrow--left:after {
  border-top-width: 0;
  border-right-width: 0;
  border-bottom-width: medium;
  border-left-width: medium;
  left: 10px;
}

.arrow--right:after {
  border-top-width: medium;
  border-right-width: medium;
  border-bottom-width: 0;
  border-left-width: 0;
  right: 10px;
}

